import {withPrefix} from 'gatsby';
import React from 'react';

import {Button} from '../../../components/button';
import {Layout} from '../../../components/layout';

const locale = 'en';

export default () => (
    <Layout
        title="Responsible disclosure policy"
        description="Found a vulnerability in one of our software products?"
        ogImage="security-en.png"
        translatedPagePath="/nl/voorwaarden/veiligheid/"
        locale={locale}>
        <section className="section">
            <article className="content content--no-hero">
                <div className="container">
                    <div className="content__display">
                        <h2>
                            {/* Make sure to have the correct spacing when br becomes invisible */}
                            Responsible <br /> disclosure
                        </h2>
                        <div className="display__number">RD</div>
                    </div>
                    <div className="content__copy">
                        <p className="p-lead">
                            At Label305 we believe the safety of the software products we develop, as well as our own
                            systems is of the utmost importance. But no matter how much effort we put into system
                            security, there can still be vulnerabilities present.
                        </p>
                        <p>
                            If you have found a vulnerability in one of our own systems, please let us know so that we
                            can take action <span className="highlight">as soon as possible</span>. We would like to
                            work with you, so we are able to protect our end users, our clients and our systems.
                        </p>
                        <p>
                            Have you found a vulnerability in one of the software products we develop for a client? Also
                            then we would like to hear from you, so that we can get to work quickly. Unfortunately we
                            cannot always speak for our client. Therefore, first check the responsible disclosure policy
                            of the software product itself.
                        </p>
                        <p>
                            We strive to solve all problems as quickly as possible and we are happy to be involved with
                            a possible publication about the problem after it has been solved.
                        </p>
                        <p>Please do the following:</p>
                        <ul>
                            <li>
                                mail your findings to{' '}
                                <a href="mailto:security@305.nl" title="Email us with a security alert">
                                    security@305.nl
                                </a>
                                . Encrypt your findings using our{' '}
                                <a href={withPrefix('/files/security.asc')}>PGP key</a> to prevent this critical
                                information from falling into the wrong hands,
                            </li>
                            <li>
                                <span className="highlight">Do not take advantage</span> of the vulnerability or problem
                                you have discovered, for example by downloading more data than necessary to demonstrate
                                the vulnerability or deleting or modifying other people's data,
                            </li>
                            <li>Do not reveal the problem to others until it has been resolved,</li>
                            <li>
                                Do not use attacks on physical security, social engineering, distributed denial of
                                service, spam or applications of third parties, and
                            </li>
                            <li>
                                Do provide sufficient information to reproduce the problem, so we will be able to
                                resolve it as quickly as possible. Usually, the IP address or the URL of the affected
                                system and a description of the vulnerability will be sufficient, but complex
                                vulnerabilities may require further explanation.
                            </li>
                        </ul>
                        <p>What we promise:</p>
                        <ul>
                            <li>
                                We will respond to your report within 3 business days with our evaluation of the report
                                and an expected resolution date,
                            </li>
                            <li>
                                If you have followed the instructions above, we will not take any legal action against
                                you in regard to the report,
                            </li>
                            <li>
                                We will handle your report with{' '}
                                <span className="highlight">strict confidentiality</span>, and not pass on your personal
                                details to third parties without your permission,
                            </li>
                            <li>We will keep you informed of the progress towards resolving the problem,</li>
                            <li>
                                In the public information concerning the problem reported, we will give your name as the
                                discoverer of the problem (unless you desire otherwise), and
                            </li>
                            <li>
                                As a token of our gratitude for your assistance, we offer a reward for every report of a
                                security problem that was not yet known to us. The amount of the reward will be
                                determined based on the severity of the leak and the quality of the report. The minimum
                                reward will be a <span className="highlight">&euro;&nbsp;50,- gift certificate</span>.
                            </li>
                        </ul>
                        <p>
                            <small>
                                This text is based on{' '}
                                <a
                                    href="https://www.responsibledisclosure.nl/en/"
                                    rel="external nofollow"
                                    title="Original Responsible Disclosure website">
                                    a text
                                </a>{' '}
                                written by Floor Terra (
                                <a
                                    href="https://creativecommons.org/licenses/by/3.0/"
                                    rel="external nofollow"
                                    title="Text of the Creative Commons license">
                                    Creative Commons Attribution 3.0
                                </a>
                                ).
                            </small>
                        </p>
                    </div>

                    <div className="content__buttons content__buttons--50">
                        <Button
                            url="mailto:security@305.nl"
                            name="security@305.nl"
                            title="Email us with a security alert"
                        />
                    </div>
                </div>
            </article>

            <article className="content content--no-hero">
                <div className="container">
                    <div className="content__display">
                        <h2>
                            <span>Exclusions</span>
                        </h2>
                        <div className="display__number">E</div>
                    </div>
                    <div className="content__copy">
                        <p className="p-lead">
                            We have <span className="highlight">no rewards</span> for trivial or unexploitable security
                            problems. Below you will find a number of examples of known vulnerabilities and accepted
                            risks for which we do not reward a bounty.
                        </p>
                        <ul>
                            <li>HTTP server response with a non-200 status code;</li>
                            <li>Fingerprinting and version banner disclosure;</li>
                            <li>
                                Everything related to our use of{' '}
                                <a href="https://www.homerun.co" rel="external nofollow">
                                    Homerun
                                </a>{' '}
                                at{' '}
                                <a href="https://careers.305.nl" rel="external nofollow">
                                    https://careers.305.nl
                                </a>
                                ;
                            </li>
                            <li>
                                Publicly accessible files and folders with non-sensitive information&mdash;e.g.
                                robots.txt;
                            </li>
                            <li>Clickjacking and related vulnerabilities;</li>
                            <li>
                                CSRF on logout function or on forms available without a session&mdash;e.g. a contact
                                form;
                            </li>
                            <li>Presence of 'auto-complete' or 'save password' feature support;</li>
                            <li>Lack of 'Secure' and 'HTTP Only' flags on non-sensitive cookies;</li>
                            <li>A weak CAPTCHA or CAPTCHA bypassing;</li>
                            <li>Brute force on 'forgot password' form or 'account lockout' not enforced;</li>
                            <li>Allowed HTTP requests with OPTIONS method;</li>
                            <li>
                                Username or e-mail address enumeration with brute force attempts via login error
                                messages or 'forgot password' error messages;
                            </li>
                            <li>
                                Lack of HTTP headers such as: Strict-Transport-Security, X-Frame-Options,
                                X-XSS-Protection, X-Content-Type-Options, Content-Security-Policy,
                                X-Content-Security-Policy, X-WebKit-CSP;
                            </li>
                            <li>
                                SSL configuration weaknesses including: SSL attacks that cannot be exploited externally,
                                lack of SSL 'Forward Secrecy' and supporting unsafe cipher suites;
                            </li>
                            <li>The lack of HTTP Public Key Pinning (HPKP);</li>
                            <li>Incorrectly configured SPF, DKIM, DMARC records;</li>
                            <li>'Host' header injection;</li>
                            <li>Content spoofing and text injection on pages with an error message;</li>
                            <li>Reporting old software versions without a proof-of-concept of a working exploit;</li>
                            <li>The leakage of information through metadata;</li>
                            <li>Missing DNSSEC;</li>
                            <li>Expired or inactive domain names;</li>
                            <li>Same Site Scripting or use of a localhost DNS record.</li>
                        </ul>
                    </div>
                </div>
            </article>
        </section>
    </Layout>
);
